html {
  box-sizing: border-box;
  width: 100%;
  max-width: 100dvw;
}

body {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: var(--font-family);
  color: rgba(var(--cl-a), 1);
  background-color: var(--body-bg);
  -webkit-text-size-adjust: 100%;
}
body.fixed-body {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
body.admin-bar header {
  top: 32px;
}

main {
  min-width: 0 !important;
}
main.no-padding {
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  margin: 0;
  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
  display: inline-flex;
  margin: 0;
  padding: 0;
  line-height: initial;
  color: inherit;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

p {
  color: rgba(var(--cl-a), 1);
  font-family: var(--font-family);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 168.52%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

label {
  display: inline-flex;
}

input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
}
input::placeholder {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
}
input[type=number] {
  appearance: textfield;
}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

textarea {
  border: none;
  outline: none;
  resize: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  position: relative;
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.panel,
[data-content] {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

[data-tab-content] {
  display: none;
}
[data-tab-content].active {
  display: block;
}

.dis-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  overscroll-behavior: none !important;
  scroll-behavior: none !important;
}

.overlay {
  position: fixed;
  height: 100%;
  display: flex;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 20;
  padding: 20px 20px;
  overflow-y: auto;
  background: rgba(var(--bg-d), 0.8);
  visibility: hidden;
  opacity: 0;
  transition: all 1s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay.active {
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}
.overlay.active-mode {
  z-index: 100;
}
.overlay.active-drawer {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
  transition: all 0.6s;
  background-color: transparent;
}

*::-webkit-scrollbar {
  width: 8px;
  height: 7px;
  cursor: pointer;
}
@media (max-width: 767px) {
  *::-webkit-scrollbar {
    display: none;
  }
}
*::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
  transition: all 0.2s;
}
*::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
}
*::-webkit-scrollbar-button {
  display: none;
  background-color: transparent;
  box-shadow: none;
}

[data-btn-modal] svg {
  pointer-events: none;
}

.modal {
  position: absolute;
  margin: auto;
  transform: scale(0.8);
  display: none;
  overflow: initial !important;
}
.modal.active {
  position: initial;
  transform: none;
  transform: scale(1);
}
.modal .close svg {
  pointer-events: none;
}

