.main-title {
  --size: clamp(57px, 11.905vw, 117px);
  color: rgba(var(--cl-d), 1);
  text-align: center;
  font-family: var(--font-family);
  font-size: var(--size);
  font-style: normal;
  font-weight: 600;
  line-height: var(--size);
  letter-spacing: calc(clamp(0px, 0.714vw, 6px) * -1);
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-title * {
  font-style: inherit;
}

.accent i {
  font-style: inherit;
  font-size: inherit;
  font-weight: 500;
  letter-spacing: inherit;
  color: rgba(var(--cl-c), 1);
}

.main-button {
  height: 77px;
  max-width: 320px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(var(--bg-e), 1);
  color: rgba(var(--cl-a), 1);
  font-family: var(--font-family);
  font-size: 15.805px;
  font-style: normal;
  font-weight: 300;
  line-height: 17px; /* 107.563% */
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  box-shadow: 0px 0px 0px -9px rgba(238, 108, 61, 0);
  transition: all 0.4s;
  transition-delay: 0ms;
}
.main-button:before {
  left: auto;
  right: 0;
  top: 0;
  width: 0;
  bottom: 0;
  position: absolute;
  content: "";
  background-color: rgba(var(--bg-d), 1);
  transition: all 0.4s;
  z-index: -1;
  transition-delay: 100ms;
}
@media (any-hover: hover) {
  .main-button:hover {
    transition-delay: 100ms;
    box-shadow: 0px 0px 40px -9px rgb(238, 108, 61);
  }
  .main-button:hover:before {
    left: 0;
    right: auto;
    width: 100%;
    transition-delay: 0ms;
  }
}

.main-nav__list {
  display: flex;
  align-items: center;
  gap: 30px clamp(30px, 3.90625vw, 60px);
}
.main-nav a {
  color: rgba(var(--cl-a), 1);
  font-family: var(--font-family);
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  text-transform: uppercase;
  transition: color 0.3s;
}
@media (any-hover: hover) {
  .main-nav a:hover {
    color: rgba(var(--cl-d), 1);
  }
}

.main-top {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 50px;
}